import React from 'react';
import Link from '../Link';
import s from './SiteNavigation.module.css'

const closeNavigation = (e) => {
  triggerTransition('transition name');
  console.log('close nav triggered');
  let navTrig = document.querySelector('#nav-trigger')
  if(navTrig.checked) {
      navTrig.checked = false
  }
}

const triggerTransition = (name) => {
  console.log(name);
}

const SiteNavigation = () => {
  return (
    <nav className={s['site-navigation']}>
      <ul>
        <li className={s['site-navigation__item']}><Link to="/" handleClick={closeNavigation}>Home</Link></li>
        <li className={s['site-navigation__item']}><Link to="/work" handleClick={closeNavigation}>Work</Link></li>
        <li className={s['site-navigation__item']}><Link to="/about" handleClick={closeNavigation}>About</Link></li>
        <li className={s['site-navigation__item']}><Link to="/blog" handleClick={closeNavigation}>Blog</Link></li>
      </ul>
    </nav>
  )
}

export default SiteNavigation


