import React from "react"
import * as PropTypes from "prop-types"
import Footer from "../components/Footer"
import Header from "../components/Header"
import SiteNavigation from '../components/SiteNavigation'
import SiteContainer from '../components/SiteContainer'
import { rhythm } from "../utils/typography"
import s from './LayoutStyles.module.css'


const propTypes = {
  children: PropTypes.func.isRequired,
}

class DefaultLayout extends React.Component {
  
  render() {
    return (
      <div className={s['body-wrapper']}>
        <input type="checkbox" id="nav-trigger" className={s['nav-trigger']} />
        <label htmlFor="nav-trigger" className={s['nav-trigger-label']}>
          <span className={s['nav-trigger-label__bar']}></span>
          <span className={s['nav-trigger-label__bar']}></span>
          <span className={s['nav-trigger-label__bar']}></span>
        </label>
        <div className={s['site-wrapper']}>
          <SiteNavigation />
          <SiteContainer>
            <Header />
            
            {this.props.children()}
            
            <hr style={{ marginTop: rhythm(3) }} />
            <Footer />
          </SiteContainer>
        </div>
      </div>
    )
  }
}

DefaultLayout.propTypes = propTypes

export default DefaultLayout



